/* Base Variables */
:root {
  --silhouette-to-bg-position: 30%;
  --text-to-right: 28.5%;
  --moon-to-position: 85% center;
  --moon-to-margin-top: -10vh;
  --moon-to-transform: translate(5%, -30%);
  --contrast: 120%;
  background-color: black;
}

/* Header */
#welcome-section {
  background-image: linear-gradient(to bottom, #181d23 0%, #202736 80%);
  background-attachment: scroll;
  position: relative;
  z-index: 20;
  animation: moveBackground 10s linear infinite;
}

@keyframes moveBackground {
  0% {
    background-position: center top;
  }
  100% {
    background-position: center bottom;
  }
}

#welcome-section::before {
  content: "";
  position: absolute;
  z-index: 10;
  opacity: 1;
  animation: stars-move-in 1000ms 300ms forwards;
}

@keyframes stars-move-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.forest {
  position: relative;
  bottom: -50vh;
  background: url(/evonne/static/media/bg-f.a9db5695.png) bottom left repeat-x;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  opacity: 1;
  animation: forest-move-in 1000ms 500ms forwards;
}

@keyframes forest-move-in {
  from {
    background-position-y: 150%;
  }
  to {
    background-position-y: 0%;
    opacity: 1;
  }
}

.silhouette {
  position: absolute;
  bottom: 0%;
  background: url(/evonne/static/media/evonne-figure.b1b68eae.png) bottom left no-repeat;
  background-size: 30%;
  width: 60%;
  height: 60%;
  z-index: 20;
  opacity: 0;
  animation: silhouette-move-in 1000ms 800ms forwards;
}

@keyframes silhouette-move-in {
  from {
    background-position-x: 0;
  }
  to {
    opacity: 1;
    background-position-x: var(--silhouette-to-bg-position);
  }
}

.moon {
  width: 100%;
  height: 100%;
  /* bottom: 30%; */
  left: 2vw;
  position: absolute;
  background: url(/evonne/static/media/book-cover.eeb1615e.png) 25% 20% no-repeat;
  background-size: 18% auto;
  z-index: 20;
  opacity: 0;
  animation: moon-move-in 1.2s 1s forwards;
}

@keyframes moon-move-in {
  from {
    opacity: 0;
    background-position: left 100%;
  }
  to {
    opacity: 1;
    margin-top: var(--moon-to-margin-top);
    transform: translate(5%, -15%);
    background-position: var(--moon-to-position);
  }
}

