}

/* Hover Me #1 */
#about .hvr::before {
  position: absolute;
  background: var(--primary-inv);
  opacity: .7;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  transition: transform .6s ease;
  transform: scaleX(0);
  transform-origin: right;
}

#about .hvr:hover::before {
  transform: scaleX(1);
  transform-origin: left;
}

#about .sd-container {
  &-container {
    position: fixed;
    width: 100%;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%); /* Changed from translateY to translateX */
    display: flex;
    justify-content: center; /* Center horizontally */
    align-items: center;
  }
}

#about .sd-container .arrow {
  position: relative;
  width: 42px;
  height: 16px;
  opacity: 0;
  margin: 0 auto; /* Center the arrow within its container */
  
  &::before,
  &::after {
    content: "";
    width: 21px;
    height: 2px;
    background-color: #00ccd3;
    border-radius: 2px;
    display: inline-block;
  }
  
  &::before {
    transform: rotate(45deg) translateX(25%)
  }
  
  &::after {
    transform: rotate(-45deg) translateX(-25%)
  }
  
  &:nth-child(1) {
    top: -50px;
    opacity: 1;
    animation: arrow2 1s ease 0s infinite;
  }
  
  &:nth-child(2) {
    top: -25px;
    animation: arrow1 1s ease 0.25s infinite;
  }
}

@keyframes arrow1 {
  from {
    opacity: 0;
    top: -25px;
  }
  
  to {
    opacity: 0;
    top: 0px;
  }
  
  50% {
    opacity: 1;
  }
}

@keyframes arrow2 {
  from {
    opacity: 0;
    top: -35px;
  }
  
  to {
    opacity: 0;
    top: -10px;
  }
  
  50% {
    opacity: 1;
  }
}
#book {
    min-height: 50vh;
    font-size: 1.4rem;
    position: relative;
    background: linear-gradient(215deg, #f0f0f0 0%,#fafafa 100%);
    margin-top: -10rem;
    z-index: 1;
  }
  
  #book a,
  #book a:visited {
    color: #f300b4;
  }
  
  #book a:hover,
  #book a:active {
    color: #252934;
  }
  
  /* Container */
  #book .book-container {
    max-width: 1400px;
    margin: 0 auto;
    width: 100%;
    padding: 12rem 5rem 8rem;
  }
  
  /* Heading */
  #book .book-container .heading  h3{
    font-size: 2.4rem;
    margin-bottom: 2rem;
    text-align: center;
  }
  
  
  #book .heading .separator {
    background: #f300b4;
    width: 150px;
    height: 2px;
    margin: 1rem auto;
  }
  
  #book .heading .subtitle {
    font-size: 1.6rem;
    text-align: center;
    color: inherit;
    padding-bottom: 1.5rem;
  } 

  #book .heading  ul {
    list-style-type: circle;
    justify-content: center;
    display: inline-block;
    margin: 2rem 2rem 1rem 1rem;
  }
  
  
/* Single Project */
  #book .project {
    margin: 1rem auto;
    width: 70%;
    padding: 2rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 2rem;
  }
  
  /* Project Image */
  #book .project .project-link {
    display: block;
    margin: auto 0;
    color: #252934;
    overflow: hidden;
    text-align: center;
    border-radius: 50%;
    border: 1px solid #fafafa;
    box-shadow: 0 20px 10px -10px #25293450;
    transition: 300ms;
  }
  
  #book .project .project-link:hover {
    box-shadow: 0 50px 15px -30px #25293450;
  }
  
  #book .project .project-link:hover > img {
    filter: saturate(1);
    transform: scale(1.05);
  }
  
  #book .project .project-image {
    width: 100%;
    transform: scale(1.2);
    filter: saturate(0);
    transition: all 300ms;
  }
  
  /* Project Details */
  #book .project .project-details {
    margin: auto 0;
  }
  
  #book .project-details .project-tile {
    font-size: 2rem;
    text-transform: uppercase;
    font-weight: bold;
    margin-bottom: 0;
    color: #f300b4;
  }
  
  /* Icons */
  #book .project-details .icons {
    margin: 0;
    color: #252934;
  }
  
  #book .project-details .icons i {
    margin-right: .4rem;
    font-weight: normal;
    font-size: 1.4rem;
  }
  
  /* Text */
  #book .project-details small {
    font-style: italic;
  }
  
  #book .project-details p {
    margin: 1rem 0;
  }
  
  /* Buttons */
  #book .project-details .buttons {
    display: flex;
    justify-content: space-between;
  }
  
  #book .project-details .buttons a {
    width: 49%;
    padding: .5rem;
    border: none;
    border-bottom: 1px solid #f300b4;
    color: #252934;
    background: #fafafa;
    font-size: 1.2rem;
    text-align: center;
  }
  
  #book .project-details .buttons a:hover {
    background: #f300b4;
    color: #fafafa;
  }
  #book .project-details .buttons i {
    font-size: .8rem;
    vertical-align: middle;
    margin-left: .5rem;;
  }
  
  
  @media only screen and (max-width: 1149px) {
    #book .project {
      grid-template-columns: 1fr 2fr;
    }
  }
  
  @media only screen and (max-width: 949px) {
    #book .project {
      grid-template-columns: 1fr;
    }
  }
  
  @media only screen and (max-width: 649px) {
    #book {
      background: #f0f0f0;
    }
    #book .book-container {
      padding: 12rem 0 8rem;
    }
    #book .project {
      padding: 2rem 0;
    }
  }
  
