    padding: 50px 0 100px;
    list-style-type: none;
}

#testimonial .tabs-controls-link {
    position: relative;
    display: inline;
    padding: 16px 32px;
    font-size: 18px;
    min-width: 20px;
    font-weight: 400;
    color: black;
    text-transform: uppercase;
    text-decoration: none;
}

#testimonial .tabs-controls-link:after {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    display: block;
    width: 0;
    height: 4px;
    background-color: #00b1b2;
    border-radius: 2px;
    margin: auto;
