}


/* Heading */
#free-resources .heading .title {
  text-align: center;
  font-size: 2.4rem;
  line-height: 2.4rem;
}

#free-resources .heading .separator {
  background: #f300b4;
  width: 150px;
  height: 2px;
  margin: 1rem auto;
}

#free-resources .heading .subtitle {
  font-size: 1.4rem;
  width: 100%;
  margin: 0 auto;
  text-align: center;
}

/* Single Project */
#free-resources .free-resource {
  margin: auto;
  width: 90%;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 2rem;
  max-height: -moz-fit-content;
  max-height: fit-content;
}

#free-resources .free-resource-columns {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 3rem;
  gap: 3rem;
}

#free-resources .free-resource-group {
  display: flex;
  flex-direction: column;
  grid-gap: 2rem;
  gap: 2rem;
}

/* Project Image */
#free-resources .free-resource .free-resource-link {
  display: block;
  margin: auto 0;
  color: #252934;
  max-width: 20vw;
  overflow: hidden;
  text-align: center;
  border-radius: 50%;
  border: 1px solid #fafafa;
  box-shadow: 0 20px 10px -10px #25293450;
  transition: 300ms;
}

#free-resources .free-resource .free-resource-link:hover {
  box-shadow: 0 50px 15px -30px #25293450;
}

#free-resources .free-resource .free-resource-link:hover > img {
  filter: saturate(1);
  transform: scale(1.25);
}

#free-resources .free-resource .free-resource-image {
  transform: scale(1.2);
  filter: saturate(0);
  width: 100%;
  transition: all 300ms;
}

/* Project Details */
#free-resources .free-resource .free-resource-details {
  margin: auto 0;
}

#free-resources .free-resource-details .free-resource-tile {
  font-size: 1.2rem;
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 0;
  color: #f300b4;
}

/* Icons */
#free-resources .free-resource-details .icons {
  margin: 0;
  color: #252934;
}

#free-resources .free-resource-details .icons i {
  margin-right: .4rem;
  font-weight: normal;
  font-size: 1.4rem;
}

/* Text */
#free-resources .free-resource-details small {
  font-style: italic;
}

#free-resources .free-resource-details p {
  margin: 1rem 0;
}

/* Buttons */
#free-resources .free-resource-details .buttons {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}

#free-resources .free-resource-details .buttons a {
  width: 49%;
  padding: .5rem;
  border: none;
  border-bottom: 1px solid #f300b4;
  color: #252934;
  background: #fafafa;
  font-size: 1.2rem;
  text-align: center;
}

#free-resources .free-resource-details .buttons a:hover {
  background: #f300b4;
  color: #fafafa;
}
#free-resources .free-resource-details .buttons i {
  font-size: .8rem;
  vertical-align: middle;
  margin-left: .5rem;;
}


@media only screen and (max-width: 1149px) {
  #free-resources .free-resource {
    grid-template-columns: 1fr 2fr;
  }
}

@media only screen and (max-width: 949px) {
  #free-resources .free-resource {
    grid-template-columns: 1fr;
  }
}

@media only screen and (max-width: 767px) {
  #free-resources .free-resource-columns {
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    gap: 2rem;
  }
  
  #free-resources .free-resource .free-resource-link {
    max-width: 60vw;
    margin: 0 auto;
  }
}

@media only screen and (max-width: 480px) {
  #free-resources {
    padding: 6rem 2rem 6rem;
  }
  
  #free-resources .free-resource-columns {
    grid-template-columns: 1fr;
  }
  
  #free-resources .heading .title {
    font-size: 2rem;
  }
  
  #free-resources .free-resource {
    width: 100%;
  }
}

#contact {
  background: #181d23 url(/evonne/static/media/envelope.b0ec457f.svg) no-repeat right;
  background-size: contain;
  color: #fafafa;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 100px 0;
  z-index: 1;
}

#contact .container {
  width: 70%;
  max-width: 1200px;
  padding: 2vh 0;
}
#contact .container .social {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* margin-top: 1rem; */
}

#contact .container .social a {
  color: #fafafa;
  transition: color 0.3s ease;
}

#contact .container .social a:hover {
  color: #f300b4;
}


#contact .container .heading-wrapper img {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 80%;
}


.heading-wrapper .heading .title {
  font-size: 3rem;
  line-height: 2.4rem;
}

.heading-wrapper .heading .separator {
  background: #f300b4;
  width: 150px;
  height: 2px;
  margin: 1rem 0;
}

.heading-wrapper .heading .subtitle {
  font-size: 1.4rem;
}

#contact-form {
  margin-top: 1rem;
}

input, textarea {
  border: none;
  padding: 1rem;
  font-family: 'Overlock', Arial, Helvetica, sans-serif;
  width: 100%;
  height: 40%;
  transition: 200ms;
}

input[type="text"],
input[type="email"],
input[type="text"]:not(output):not(:focus),
input[type="email"]:not(output):not(:focus),
textarea {
  border-bottom: 1px solid #fafafa;
  background: transparent;
  color: #fafafa;
  font-size: 1.8rem;
  box-shadow: none;
  outline: none;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="text"]:not(output):focus,
input[type="email"]:not(output):focus,
textarea:focus {
  border-bottom: 1px solid #f300b4;
}

input[type="submit"] {
  background: #f300b4;
  color: #fafafa;
  margin-top: 1rem;
  width: auto;
  float: right;
  cursor: pointer;
  transition: all 0.3s ease;
}

input[type="submit"]:hover,
input[type="submit"]:focus {
  color: #333;
  background: #fafafa;
}

::placeholder {
  color: #fafafa;
}

/** Email to avoid spam **/
.mail {
  display: inline-block;
  font-style: italic;
}

.mail .at, .mail .dot {
  font-size: .9rem;
  margin: 0 .1rem;
  color: #046565;
}
/* Responsive Breakpoints */
@media only screen and (max-width: 1149px) {
  #contact .social a {
    display: block;
    margin-bottom: 0.5rem;
  }
  
  .heading-wrapper .heading .title {
    font-size: 2.5rem;
    line-height: 2.2rem;
  }
}

@media only screen and (max-width: 768px) {
  #contact .container {
    width: 80%;
  }
  
  .heading-wrapper .heading .title {
    font-size: 2.2rem;
    line-height: 2rem;
  }
  
  .heading-wrapper .heading .subtitle {
    font-size: 1.2rem;
  }
  
  input[type="text"],
  input[type="email"],
  textarea {
    font-size: 1.5rem;
  }
}

@media only screen and (max-width: 649px) {
  #contact {
    clip-path: polygon(0 0, 20% 5%, 100% 0, 100% 100%, 0 100%);
    padding: 60px 0;
    background-position: center right;
    background-size: cover;
  }
  
  #contact .container {
    width: 90%;
    padding: 1vh 5%;
  }
  
  .heading-wrapper .heading .title {
    font-size: 1.8rem;
    line-height: 1.8rem;
  }
  
  .heading-wrapper .heading .separator {
    width: 100px;
  }
}

@media only screen and (max-width: 480px) {
  #contact {
    padding: 40px 0;
  }
  
  #contact .container {
    width: 95%;
  }
  
  .heading-wrapper .heading .title {
    font-size: 1.5rem;
    line-height: 1.5rem;
  }
  
  input[type="text"],
  input[type="email"],
  textarea {
    font-size: 1.2rem;
    padding: 0.7rem;
  }
  
  input[type="submit"] {
    padding: 0.7rem 1.2rem;
    font-size: 1rem;
  }
  
  #contact .container .heading-wrapper img {
    max-width: 95%;
  }
}
#speaking {
  height: 100%;
  min-height: 100vh;
  font-size: 1.4rem;
  position: relative;
  z-index: 1;
  background: #fafafa url(/evonne/static/media/Author_BG.c30482c0.png) center right no-repeat;
  background-attachment: fixed;
  --primary-inv: #333;
}

#speaking .wrapper {
  padding: 10rem 5rem;
  height: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding-bottom: 2rem;
}

#speaking article {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 2rem;
  padding: 3rem 0;
}

#speaking .title {
  grid-column: 1 / -1;
  display: flex;
  flex-direction: column;
  align-items: left;
  text-align: left;
}

#speaking .title h3 {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 1rem;
}

#speaking .title h4 {
  margin-top: 1rem;
  font-size: 1.6rem;
  margin-bottom: 1.5rem;
}
#speaking .title ul {
  list-style: none;
  padding: 0;
  margin: 2rem 0;
}


#speaking .separator {
  background: #f300b4;
  width: 100px;
  align-self: center;
  height: 2px;
  margin: 1rem 0;
  animation: pulse-grow 20s infinite ease-in-out;
}

@keyframes pulse-grow {
  0%, 100% {
    width: 100px;
  }
  50% {
    width: 45vw;
  }
}

#speaking .subtitle {
  font-size: 1.4rem;
  text-align: center;
  color: inherit;
  padding-bottom: 1rem;
}

#speaking p {
  padding-bottom: 1rem;
  color: #555;
  line-height: 1.6rem;
  text-align: left;
}
#speaking li{
  list-style: circle;
  padding-bottom: 1rem;
  color: #555;
  line-height: 1.6rem;
  text-align: left;
  margin-left: 2rem;
}


#speaking .desc.full {
  grid-column: 1 / -1;
  margin-bottom: 2rem;
  text-align: left;
}

#speaking .desc {
  background: #ffffffaa;
  padding: 1.5rem;
  text-align: justify;
}

#speaking .book-cover {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

#speaking .hyperlink {
  text-decoration: underline;
  color: var(--primary-inv);
  transition: color 0.3s ease-in-out;
  font-size: 2rem;
  position: relative;
  text-align: center;
  cursor: pointer;
  margin: 0.5rem;
  overflow: hidden;
}

#speaking img {
  position: relative;
  z-index: 1;
  margin: 0 auto;
  display: block;
  width: 50%;
  height: auto;
  border-radius: 5px;
  padding-bottom: 10rem;
}

/* Media Queries */
@media only screen and (max-width: 1149px) {
  #speaking .wrapper {
    padding: 8rem 3rem;
    padding-bottom: 2vh;
  }
}

@media only screen and (max-width: 949px) {
  #speaking {
    background-position: top left;
    background-size: cover;
  }

  #speaking .title h3 {
    font-size: 1.8rem;
  }

  #speaking .hyperlink {
    font-size: 1.8rem;
  }
}

@media only screen and (max-width: 649px) {
  #speaking .wrapper {
    padding: 5rem 1.5rem;
  }

  #speaking .title h3 {
    font-size: 1.6rem;
  }

  #speaking .title ul {
    margin: 1rem;
  }

  #speaking img {
    width: 60%;
  }
}

